.comingSection {
  padding-top:100px;
  height: 600px;
  background-image: url(../img/coming_soon/coming_soon.png);
  background-size: cover;
  color: #ffffff;
  h1 {
    margin-top: 0;
  }
  .socialIcons{
    margin-top: 40px;
    left:45%;
    position: absolute;
  }
}
