//  background-repeat: no-repeat;
//  height:600px;
//}
.comingImg{
  display: block;
  width:100%;
  overflow: hidden;
}

.voteSection{
  padding:8px 8px 4px 8px;
  h1,h2,h3,h4,h5 {
    margin-top:0 !important;
    margin-bottom:0 !important;
    padding-top:10px;
    padding-bottom:10px;
    color: #b4b4b4;
  }
  //override bootstrap
  .progress{
    margin-bottom: 0 !important;
  }
  .progress-bar{
    color: black !important;
  }
}

.socialVote{
  width:100%;
  margin-left:8px;
  // fix spacing problem when pictures are shown
  .fb-like{
    padding-bottom:4px !important;
  }
}
.voteCountdown{
  width:100%;
}

.thumbnail{
  max-width: 400px;
  min-height: 340px;
  border: 1px solid #8f8f8f !important;
}

.row-no-padding {
  [class*="col-"] {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.comingSoonEmail{
  padding-top:50px;
  padding-bottom:50px;
}

.comingsoonCaption{
  position: absolute;
}

.deadmau5 {
  top: 2%;
  left:5%;
}

.beckham {
  top:35%;
  left:60%;
}
.nfl {
  top:0;
  right:5%;
}

@media (min-width: 300px) {
  .comingsoonCaption{
    h1 {
      font-size: 16px;
    }
  }
}
@media (min-width: 480px) {
  .comingsoonCaption{
    h1 {
      font-size: 24px;
    }
  }
}
@media (min-width: 768px) {
  .comingsoonCaption{
    h1 {
      font-size: 34px;
    }
  }
}
@media (min-width: 992px) {
  .comingsoonCaption{
    h1 {
      font-size: 40px;
    }
  }
}
@media (min-width: 1170px) {
  .comingsoonCaption{
    h1 {
      font-size: 50px;
    }
  }
}
@media (min-width: 1440px) {
  .comingsoonCaption{
    h1 {
      font-size: 60px;
    }
  }
}